import { Link } from "gatsby"
import React from "react"
import { RESOURCES } from "../utils/languages"

export default function Layout({ children, title = "My Blog", lang = "EN" }) {
  return (
    <div className={`max-w-2xl mx-auto px-5 py-10 ${lang === "FA" && "rtl"}`}>
      <nav class="hidden md:flex space-x-6 border-b pb-2">
        {RESOURCES[lang].NAV.map(link => (
          <Link
            key={link.key}
            className="text-base font-medium text-gray-500 hover:text-gray-900"
            to={link.to}
          >
            {link.title}
          </Link>
        ))}
      </nav>
      <main>{children}</main>
      <footer>
        {lang === "FA" ? (
          `© ${new Date().getFullYear()}`
        ) : (
          <>
            © {new Date().getFullYear()}, Built with ❤️ in{" "}
            <a href="https://en.wikipedia.org/wiki/Sanandaj">Sanandaj</a>
          </>
        )}
      </footer>
    </div>
  )
}
